import React from 'react'
import { Link as GLink, useThemeUI, get } from 'gatsby'
import { Link, Text } from 'theme-ui'
import TextList from '@components/TextList'
import PageTitle from '@components/PageTitle'
import AvatarSimple from '@components/AvatarSimple'
import rv from '@components/utils/buildResponsiveVariant'

const styles = {
  item: {
    display: `inline-block`
  }
}

export const PostHead = ({ title, author, date, timeToRead, category }) => {
  const options = { year: 'numeric', month: 'long', day: 'numeric' }

  const arabicDate = new Date(date).toLocaleDateString('ar-ma', options)

  const info = (
    <TextList>
      {/* {category && category.slug && (
      <Text sx={styles.item}>
      {`Published in `}
      <Link variant='mute' as={GLink} to={category.slug}>
      <strong>{category.name}</strong>
      </Link>
      </Text>
    )} */}
      {/* {timeToRead && (
      <Text sx={{ ...styles.item, color: `error` }}>
         دقيقة قراءة <strong>{timeToRead}</strong> 
      </Text>
    )} */}
      {author && author.thumbnail ? (
        <Link
          as={GLink}
          to={author.slug}
          aria-label={author.name}
          // sx={{ variant: responsiveVariant }}
        >
          <AvatarSimple
            avatar={author.thumbnail}
            alt={author.name}
            size='small'
          />
        </Link>
      ) : null}
      {author && author.slug && (
        <Text sx={styles.item}>
          <Link variant='mute' as={GLink} to={author.slug}>
            <strong>{author.name}</strong>
          </Link>
        </Text>
      )}
      {arabicDate && <Text sx={styles.item}>{arabicDate}</Text>}
    </TextList>
  )

  return <PageTitle header={title} running={info} />
}
